import { colorCodes } from "./colors";
import { LogColor } from "./types";

export default function println<T>(
  helper: string,
  data: T,
  color: LogColor = "reset",
  addStackTrace: boolean = true
) {
  const formattedLog = JSON.stringify(data, null, 2);

  const stack = new Error().stack?.split("\n");
  const callerInfo = stack ? stack[2] : "unknown";

  const match = callerInfo.match(/at (\S+) \(([^)]+)\)/);
  const location = match ? match[2] : "unknown location";
  const callerFunctionName = match ? match[1] : "unknown function";

  const colorCode = colorCodes[color];

  if (addStackTrace) {
    console.log(
      `${colorCode}${helper} (called from: ${callerFunctionName} at ${location}) =>\n${formattedLog}${colorCode}`
    );
  } else {
    console.log(`${colorCode}${helper} =>\n${formattedLog}${colorCode}`);
  }
}
